.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__item {
  padding: 0 8px;
  display: inline-block;
}

.nav__item:first-child {
  padding-left: 0;
}

.nav__item:last-child {
  padding-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  display: inline-block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  display: flex;
}

.stars-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars-star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars-star:nth-child(-n+1), .stars--2 .stars-star:nth-child(-n+2), .stars--3 .stars-star:nth-child(-n+3), .stars--4 .stars-star:nth-child(-n+4), .stars--5 .stars-star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  padding: 32px 16px 16px;
  font-size: 10px;
  transition-duration: .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__rating {
  justify-content: space-between;
  align-items: flex-end;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.card__rating-reviews {
  margin: 0;
  line-height: 14px;
}

.card__product-image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__product-title {
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition-duration: .3s;
}

.card__product-title:hover {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin-block: 0 16px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  height: min-content;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-number {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.main {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  margin: 50px 40px;
  display: grid;
}

@media screen and (width >= 768px) and (width <= 1023px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 488px) and (width <= 767px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width <= 487px) {
  .main {
    grid-template-columns: repeat(1, 200px);
  }
}

body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
}

